main {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  padding: 0;
  margin: 0
}

.slider {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 750px;
    height: 600px;
    border: 5px solid black;
    border-radius: 10px;
    .slide {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        overflow: hidden;
        img {
          width: 750px;
          height: fit-content;
          border-radius: 10px;
        }
        .pictures {
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: row;
          width: 100%;
          height: 450px;
        }
        .picture {
          transition: 0.7s cubic-bezier(0.50, 0.0, 0.30, 1.0);
          &:nth-child(1) {
            position: relative;
            left: -100%;
          }
          &:nth-child(2) {
            position: relative;
            left: 0%;
          }
          &:nth-child(3) {
            position: relative;
            left: 100%;
          }
        }
    }
}

.controller {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    width: 100%;
    height: fit-content;
    i {
        font-size: 40px;
    }
    span {
        display: block;
        background-color: gray;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        margin: 0px 8px;
    }
    .active {
        background-color: red;
    }
    .btns {
        cursor: pointer;
    }
    .indicators {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: blue;
        width: 100px;
        height: 20px;
        border-radius: 10px;
    }
}
  
  .hamburgerMenu {
    position: fixed;
    width: 300px;
    height: 100%;
    right: -300px;
    background-color: #fff;
    transition: all 0.4s;
    transition-timing-function:ease-in-out;
  }
  
  .hamburgerMenu.open {
    transform: translateX(-300px);
  }
  
  .hamburgerMenu-box ul {
    list-style: none;
    padding-left: 0;
    text-align: center;
  }
  
  
  .hamburgerMenu-box ul li {
    padding: 10px 0;
  }
  
  .hamurerMenu-button {
    position: absolute;
    top: 0;
    right: 300px;
    width: 60px;
    height: 60px;
    border: solid #fff 1px;
    cursor: pointer;
  }
  
  .icon {
    width: 35px;
    height: 1px;
    background-color: #fff;
    position: absolute;
    top: 14.5px;
    right: 12.5px;
    transition: all 0.5s;
    transition-timing-function:ease-out;
  }
  
  .icon-m {
    content: '';
    width: 35px;
    height: 1px;
    background-color: #fff;
    position: absolute;
    top: 29.5px;
    right: 12.5px;
    transition: all 0.5s;
    transition-timing-function:ease-out;
  }
  
  .icon-b {
    content: '';
    width: 35px;
    height: 1px;
    background-color: #fff;
    position: absolute;
    top: 44.5px;
    right: 12.5px;
    transition: all 0.5s;
    transition-timing-function:ease-out;
  }
  
  .icon.open {
    transform: translateY(14.5px) rotate(135deg);
  }
  
  .icon-b.open {
    transform: translateY(-14.5px) rotate(-135deg);
  }
  
  .icon-m.open {
    opacity: 0;
  }